body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button {
  display: inline-block;
}

.button__button {
  margin: 1px;
  padding: 10px 15px;
  background-color: wheat;
  font-size: 16px;
  border: none;
}
.input {
  display: inline-block;
}

.input__input {
  margin: 1px;
  padding: 10px 15px;
  background-color: papayawhip;
  font-size: 16px;
  border: none;
}
.table {
  margin: 0 auto;
}
.table-body {
  background-color: papayawhip;
}

.table-body__cell {
  padding: 10px 15px;
  text-align: center;
}

.color-true { 
  background-color: lightgreen;
}

.color-false { 
  background-color: lightcoral;
}
.table-header {
  background-color: thistle;
}

.table-header__cell {
  padding: 10px 15px;
  text-align: center;
}
.app-header {
    text-align: center;
}
.input-form {
  margin: 20px 0;
  text-align: center;
}
